import React from 'react';
import PropTypes from 'prop-types';
import { Field, reduxForm } from 'redux-form/immutable';

const fetchResults = (values, dispatch) => dispatch({ type: 'fetchResults', payload: values });

const Filters = ({ handleSubmit }) => (
  <form onSubmit={handleSubmit}>
    <table className="w3-table w3-striped w3-bordered w3-border w3-white w3-margin-bottom w3-margin-top">
      <tbody>
        <tr>
          <td>
            <Field
              component="input"
              name="issuer"
              type="text"
              className="form-control"
              id="issuer_name"
              placeholder="Issuer Name\Desc"
            />
          </td>
          <td>
            <Field
              name="category"
              className="form-control"
              component="select"
            >
              <option>Search category</option>
            </Field>
          </td>
          <td>
            <Field
              name="business"
              className="form-control"
              component="select"
            >
              <option>Search business</option>
            </Field>
          </td>
          <td>
            <Field
              name="productCategory"
              className="form-control"
              component="select"
            >
              <option>Search product category</option>
            </Field>
          </td>
          <td>
            <Field
              component="input"
              name="issuer"
              type="text"
              className="form-control"
              id="issuer_name"
              placeholder="Product"
            />
          </td>
          <td>
            <button type="submit">Search</button>
          </td>
        </tr>
      </tbody>
    </table>
  </form>
);

Filters.propTypes = {
  handleSubmit: PropTypes.func
};

export default reduxForm({
  form: 'filters',
  onSubmit: fetchResults
})(Filters);
