import { put, call, takeLatest } from 'redux-saga/effects';
import authorizedRequest from '../core/restClient';

function* handleFilterRequest(action) {
  const response = yield call(
    authorizedRequest,
    'post',
    '/filter-offers',
    action.payload.toJS()
  );
  yield put({ type: 'setFilterResults', payload: response });
}

export default function* watchHandleFilterRequest() {
  yield [
    takeLatest(
      'fetchResults',
      handleFilterRequest
    )
  ];
}
