export default function* authorizedRequest(method, url, body) {
  const requestParams = {
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
    },
    method
  };
  if (body) {
    requestParams.body = JSON.stringify(body);
  }

  // return yield fetch(`api/${url}`, requestParams).then((httpResponse) => {
  return yield fetch(url, requestParams).then((httpResponse) => {
    if (httpResponse.ok) {
      return httpResponse.json();
    }
    // Response error
    throw new Error(`Could not perform request (${httpResponse.statusText})`);
  }).then((responseJson) => {
    if (responseJson.hasError) {
      throw new Error('Something went wrong');
    }
    return responseJson;
  });
}
